 <template>
  <div id="page-user-view">


    <div class="px-12">
     <!-- Product info -->
     <div class="flex flex-wrap items-center mb-6">
       <div class="flex-grow">
         <div class="flex flex-wrap ">
           <h2 class="font-bold"
               style="line-height: 48px !important; display:flex;  vertical-align: middle !important;   ">
              {{ product.product_name }}
           </h2>
          </div>

       </div>

      </div>

      <div>
        <vs-row class="mb-12">
          <div class="vx-col md:w-1/3  ">
            <vx-card  >
              <div class="flex flex-wrap">
                <div class="flex-grow">
                  <h4>Product Image</h4>
                </div>

                <div class="flex">
                  <vs-button @click="showImageForm" color="secondary">Add Image</vs-button>
                </div>

              </div>

              <div style="max-width: 198px">
                <img :src="product.thumbnail_img" alt="content-img" class="responsive card-img-top">
              </div>
            </vx-card>
          </div>

          <div class="vx-col md:w-1/2 pl-12  ">
            <h4>Product Description</h4>
            <p> {{ product? product.product_description : "No product description"}} </p>
          </div>

        </vs-row>
      </div>


     <!-- [end] Product info -->
      <vs-divider class="mb-5"></vs-divider>


      <!-- Variant Heading -->
     <div class="flex flex-wrap items-center mb-6">
       <div class="flex-grow">
         <div class="flex flex-wrap ">
           <h2 class="font-bold"
               style="line-height: 48px !important; display:flex;  vertical-align: middle !important;   ">
              Products Variants
           </h2>

           <vs-button icon="refresh" class="py-0 ml-4" type="flat" @click="fetchProductVariants()">Refresh</vs-button>
         </div>

       </div>

       <vs-button icon="add" @click="showVariantForm()">Add Variant</vs-button>
     </div>
     <!-- [end] Variant Heading -->


      <!-- Variant List -->
     <vs-table stripe v-model="productVariantInstance" :data="productsVariants" @selected="showVariantDetails">

       <template slot="thead">
         <vs-th>Variant Name</vs-th>
         <vs-th>Unit</vs-th>
         <vs-th>Restocking Quantity</vs-th>
         <vs-th>Status</vs-th>
         <vs-th>Action </vs-th>
         <vs-th> </vs-th>
       </template>

       <template slot-scope="{data}">
         <vs-tr :data="item" :key="rowIndex" v-for="(item, rowIndex) in data">

           <vs-td :data="data[rowIndex].variant_name"> {{ data[rowIndex].variant_name }} </vs-td>
           <vs-td :data="data[rowIndex].variant_name"> {{ data[rowIndex].measuring_unit }} </vs-td>

           <vs-td :data="data[rowIndex].restocking_quantity"> {{ data[rowIndex].restocking_quantity }} </vs-td>

           <vs-td :data="data[rowIndex].vendor">
             <vs-chip v-if="data[rowIndex].is_published" color="success">Published</vs-chip>
             <vs-chip v-else color="danger">Not Published</vs-chip>
           </vs-td>


           <vs-td :data="data[rowIndex].id">
             <vs-button type="border" class="mr-4" size="small"
                        @click="showVariantForm(data[rowIndex])">Edit</vs-button>
             <vs-button   color="success" class="mr-12" size="small"
                        @click="confirmRemoval(data[rowIndex])">Publish</vs-button>
           </vs-td>

           <vs-td :data="data[rowIndex].id">
             <vs-button type="border" color="danger" class="mr-4" size="small"
                        @click="confirmRemoval(data[rowIndex])">Remove</vs-button>
           </vs-td>

         </vs-tr>
       </template>
     </vs-table>
      <!-- [End] Variant List -->
   </div>


    <!-- Variant form -->
    <vs-popup @close="closeProductVariantForm()" fullscreen title=""
              :active.sync="productVariantFormVisible">
      <div>
        <div class="flex flex-wrap">

          <div class="flex-grow">
            <h3>Product Variant Information</h3>
          </div>

        </div>

        <div class="vx-row">

          <!-- Variant information -->
          <div class="vx-col md:w-1/3 w-1/4">

            <!-- Variant Name -->
            <vs-input class="w-full mt-4" label="Variant Name" v-model="productVariantInstance.variant_name"
                      v-validate="'required'" name="name"/>
            <span class="text-danger text-sm" v-show="errors.has('name')">
              {{errors.first('name') }}
            </span>

            <!-- Variant Base Price -->
            <vs-input class="w-full mt-4" label="Base Price" v-model="productVariantInstance.base_price"
                      v-validate="'required'" name="price"/>
            <span class="text-danger text-sm" v-show="errors.has('price')">
              {{errors.first('price') }}
            </span>

            <!-- Restocking Quantity  -->
            <vs-input class="w-full mt-4" label="Restocking Quantity" v-model="productVariantInstance.restocking_quantity"
                      v-validate="'required'" name="name"/>
            <span class="text-danger text-sm" v-show="errors.has('quantity')">
              {{errors.first('quantity') }}
            </span>

            <!-- Measurement Unit -->
            <div style="height: 12px"></div>
            <span>Measurement Unit </span>
            <v-select  v-model="productVariantInstance.measuring_unit" :clearable="false"
                      :options="measurementUnits" v-validate="'required'" name="unit"
                      placeholder="Select">
            </v-select>
            <span class="text-danger text-sm" v-show="errors.has('unit')">
              {{errors.first('unit') }}
            </span>

          </div>
          <!-- [end] Variant information -->

          <!-- Variant prices -->
          <div class="vx-col md:w-1/3 w-1/4">
            <div class="flex-grow">
              <h3>Prices</h3>
            </div>
          </div>
          <!-- [end] Variant price -->

          <!-- Variant images -->
          <div class="vx-col md:w-1/3 w-1/4">
            <div class="flex flex-wrap">
              <div class="flex-grow">
                <h3>Images</h3>
              </div>

              <vs-button round   @click="closeVariantForm()" color="secondary"  >
                Add Image
              </vs-button>

            </div>
          </div>
          <!-- [end] Variant images -->

        </div>

        <div class="vx-row mt-12">
          <div class="vx-col w-full">
            <div class="flex items-start flex-col sm:flex-row">
              <!-- <vs-avatar :src="data.avatar" size="80px" class="mr-4" /> -->
              <div>
                <vs-button type="border" @click="closeProductVariantForm()" color="danger" class="mr-4 mb-4">Cancel
                </vs-button>

                <vs-button ref="saveButton" @click="saveProductVariant()" color="success" class="mr-4 mb-4">Save
                </vs-button>
              </div>
            </div>
          </div>
        </div>

       </div>
    </vs-popup>
    <!-- [end] Variant form -->


    <!-- Image Form -->
    <vs-popup @close="productImageDialog=false" fullscreen title=""
              :active.sync="productImageDialog">

      <vs-upload
        :single-upload="true"
        :action="productImageUrl" @on-success="imageUploadSuccess" @on-error="handleUploadError"/>

    </vs-popup>


  </div>
</template>

<script>

import axios from "@/axios";
import vSelect from 'vue-select'
import ProductDetails from "@/views/products/products/ProductDetails";

export default {
  components: {
    vSelect,
    ProductDetails
  },
  props: {
    product: Object
  },
  data () {
    return {
      user_data: null,
      productsVariants: [  ],
      rolesAvailable: [],
      staffStatus: [ 'Active', 'Suspended' ],

      productVariantFormVisible: false,
      productVariantInstance: {
        variant_name : '',
        base_price : '',
        restocking_quantity: '',
        measuring_unit: ''
      },

      variantDetailsDialog: false,
      measurementUnits: ['Liters','Kilograms','Meters','CCs','Bottles','Bags','Crates','Satchel','Canes'],

      /** Images */
      productImageDialog : false

    }
  },
  watch: {
    product: function (newVal) {
      this.fetchProductVariants();
    }
  },

  computed: {
    validateForm () {
      return !this.errors.any()
    },

    productImageUrl(){
      return axios.defaults.baseURL + "/config/product/image/update";
    }
  },

  methods: {
    closeProductVariants(){
      this.$emit('closeProductVariants');
    },

    fetchProductVariants() {
      console.log("Fetching variants");
      axios.get('/resources/product/variants?product_id='+this.product.id)
        .then((response) => {
             this.productsVariants = response.data.success? response.data.payload.product_variants : [];
          }).catch((error) => {
        console.log(error)
      })
    },

    /*** Adding variant **/
    showVariantForm(variant = {}){
      this.productVariantInstance = variant
      this.productVariantFormVisible = true;
    },

    closeProductVariantForm(){
      this.productVariantFormVisible = false;
    },

    saveProductVariant(){

      this.productVariantInstance.product_id = this.product.id;

      let endpoint = '/config/product/variant/add';
      if(Number.isInteger(this.productVariantInstance.id)){
          endpoint = '/config/product/variant/update';console.log("Updating...")
      }

      console.log(endpoint);

      axios.post( endpoint, this.productVariantInstance)
        .then((response) => {
         this.productVariantFormVisible = false;
         this.fetchProductVariants();
      }).catch((error) => {
        let message = "Something went wrong";
        if(error.response){
            message = error.response.data.message;
        }

        this.$vs.loading.close();
        this.$vs.notify({
          time: 12000,
          color: 'danger',
          title: 'Failed',
          text: message
        })
        console.log(error)
      })
    },
    /*** [end] Adding variant **/


    /** Images **/
    showImageForm(){
      console.log(JSON.stringify(this.productImageUrl));
      this.productImageDialog = true;
    },
    imageUploadSuccess(){
      this.$vs.notify({color:'success',title:'Upload Success',text:'Lorem ipsum dolor sit amet, consectetur'})
    },

    handleUploadError(event){
      console.log(JSON.stringify(event));

      this.$vs.notify({color:'danger',title:'Failed',
        text:'Image upload failed'})
    },
    /** [end] Images **/

    addProductImage(){
      console.log("Adding images");
    },

    confirmRemoval(variant){
      this.productVariantInstance = variant;
      this.closeProductVariants();
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: `Confirm`,
        text: 'Are you sure you want to remove ' + this.productVariantInstance.variant_name,
        accept: this.removeProductVariant
      })
    },

    removeProductVariant(){
      axios.post('/config/product/variant/remove', {
        id: this.productVariantInstance.id
      })
        .then((response) => {
          this.fetchProductVariants();
        }).catch((error) => {
        console.log(error)
      });
    },


    showVariantDetails(){
      this.variantDetailsDialog = true;
    },

    /** Resources **/
  },

  mounted () {
     this.fetchProductVariants();
  }
}

</script>

<style scoped>
.variant-form{
  border-radius: 6px;
  padding:  32px 16px;
  border: 1px solid #b2c2b2;
  background-color: #fffaf5;
}
</style>

